import DetailSwiper from "@/components/detail/DetailSwiper"
import Bottombar from "@/components/detail/BottomBar"
import NumberBox from "@/components/uni-app/uni-number-box/uni-number-box"
export default {
	components:{
		DetailSwiper,
		Bottombar,
		NumberBox},
	data() {
		return {
			isShow: false,
			animationData: {}
		}
	},
	onBackPress(e){
		if(this.isShow){
			this.hiddenClick()
			return true;
		}
	},
	onNavigationBarButtonTap(e) {
		if(e.type === 'share'){
			uni.share({
				provider:'weixin',
				type:0,
				title: 'xxx',
				scene: 'WXSenceTimeline',
				summary: 'xxxx',
				href:'www.baidu.com',
				imageUrl:'',
				success(res) {
					uni.showTabBar({
						title:'分享成功'
					})
				},
				fail(err) {
					uni.showTabBar({
						title: JSON.stringify(err)
					})
				}
				
			})
		}
	},
	methods: {
		buyClick(){
			var animation = uni.createAnimation({
			     duration: 200
			   })
			this.animation = animation
			animation.translateY(800).step();
			this.animationData = animation.export()
			this.isShow = true
			setTimeout(() => {
				animation.translateY(0).step()
				this.animationData = animation.export()
			}, 200)
		},
		addClick(){
			var animation = uni.createAnimation({
			     duration: 200
			   })
			this.animation = animation
			animation.translateY(800).step();
			this.animationData = animation.export()
			this.isShow = true
			setTimeout(() => {
				animation.translateY(0).step()
				this.animationData = animation.export()
			}, 200)
		},
		hiddenClick(){
		var animation = uni.createAnimation({
		     duration: 200
		   })
		this.animation = animation
		animation.translateY(600).step();
		this.animationData = animation.export()
		this.isShow = true
		setTimeout(() => {
			animation.translateY(0).step()
			this.isShow = false
		}, 200)
		}
	},
	
	
	
}
